import { graphql, Link, useStaticQuery } from "gatsby"
import PropTypes from "prop-types"
import React, { Component } from "react"
import styled from "styled-components"
import Img from "gatsby-image"
import { Location } from '@reach/router'
import searchmobile from "../images/searchv2.svg"
import headerbg from "../images/headerbg.svg"
//import Search from "./Search"
import Search from "../components/Search"
import {
  BrowserView,
  MobileView,
  isBrowser,
  isMobile
} from "react-device-detect";
import lineSeparator from "../images/separator.svg"
import { ReactComponent as MenuSeparator } from "../images/separator.svg"
import { ReactComponent as ExpandBtnImg } from "../images/pointer.svg"
import expandIcon from "../images/pointer.svg"
import SEO from "./seo"
import { CenteredParagraph, HeaderNumber } from "./style"
import { BioImage } from "../pages/aboutme"

const searchIndices = [
  { name: `Posts`, title: `Blog Posts`, hitComp: `PostHit` },
]

const THeader = styled.header`
  width: 100%;
  min-width: 1000px;
  background-color: #fefefe;
  z-index: 4;
  border-bottom: 1px solid #bebebe;
  height: 50px;
  position: fixed;
  top: 0;
  left: 0;
  @media only screen and (max-width: 768px) {
    min-width: auto;
    width: 100%;
  }
`

const LinkRow = styled.div`
  height: 49px;
  width: 624px;
  overflow: hidden;
  text-align: center;
  display: inline-block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%) scale(0.8);
  
  @media only screen and (min-width: 1400px) {
      transform: translateX(-50%);
  }
  
  @media only screen and (max-width: 768px) {
     width: auto;
     position: relative;
     transform: none;
     margin-top: 4px;
     left: 0;
     background-color: #fefefe;
     transition: height 0.2s ease-out;
     height: ${props => (props.active ? `180px` : `40px`)};
  }
  
`

const MainLink = styled.h1`
  display: inline-block;
  margin: 0 8px;
  font-size: 30px;
  a {
    color: #5c5c5c;
  }

`

export const BrandIcon = styled(Img)`
  width: 35px;
  display: inline-block;
  vertical-align: middle;
`

const HeaderUl = styled.ul`
  margin: 5px 0 1px;
  padding: 0;
  position:relative;
  z-index: 3;
`

const HeaderLi = styled.li`
   padding: 0 8px;
   display: inline-block;
   position: relative;
   background-image: url(${headerbg});
   background-repeat: no-repeat;
   background-position: center;
   background-size: cover;
   width: 140px;
   font-size: 16px;
   @media only screen and (max-width: 768px) {
    background: #fefefe;
    display: block;
    text-align: center;
    width: 100vw;
    font-size: larger;
    padding: 0;
  }
   
`

const HeaderLink = styled(Link)`
    padding: 10px;
    font-weight: ${props => (props.active ? `600` : `400`)};
    text-transform: uppercase;
    line-height: 42px;
    letter-spacing: .7px;
    color: ${props => (props.active ? `#748cc5` : `#6897ff`)};
    border: 0;
    &:hover {
      color: #df4d55;
    }
`

const LinkShadow = styled.div`
  width:160px;
  height: 40px;
  position: absolute;
  top:6px;
  transform:  ${props =>
    (contains(props.path, ['/blog','/post','/category'])?`translateX(155px)`:
    (props.path.includes('/aboutme')?`translateX(310px)`:
    (props.path.includes('/sandbox')?`translateX(464px)`: 
    `translateX(0.6px)` 
      )))};
  transition: transform 0.2s ease-out;
  @media only screen and (max-width: 768px) {
    display: none;
  }
`

const SubLinkShadow = styled.div`
  background-color: #748cc5;
  height: 22px;
  padding: 0;
  margin: 0;
  position: absolute;
  width: 120px;
`

const SubLinkShadow1 = styled(SubLinkShadow)`
  top: 0;
  left: 0;
`

const SubLinkShadow2 = styled(SubLinkShadow)`
  bottom: 0;
  right: 0;
`

const HeaderTitle = styled.span`
  vertical-align: top;
  font-size: 35px;
`

const Banner = styled(Link)`
border: none;
`
const SearchBar = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  margin: 10px 0;
  z-index: 10;
`

const MobileSearchBar = styled.div`
  position: fixed;
  top: 0;
  right: calc(-100vw + 50px);
  z-index: 5;
  height: 40px;
  width: 100vw;
  background-color: #fefefe;
  border-radius: 3px;
  text-align: center;
  padding-top: 10px;
  div {
    grid-gap: 0;
  }

  form {
    text-align: center;
    input {
      margin-right: 0;
      width: calc(100vw - 50px);
      border: 2px solid #bebebe;
    }
  }
   transform: ${props => (props.active ? `translateX(calc(50px - 100vw))` : `translateX(0)`)};
    transition: transform 0.3s ease-out;
  //transform: translateX(calc(50px - 100vw));
`

const MobileSearchBarCap = styled.div`
  height: 34px;
  width: 10px;
  background-color: #fefefe;
  position: fixed;
  display: inline-block;
  top: 10px;
  right: 0;
  z-index: 6;
  border-left: 2px solid #bebebe;
`

const SearchExpander = styled.div`
  width: 36px;
  height: 36px;
   background-image: url(${searchmobile});
   background-repeat: no-repeat;
   background-position: center;
   background-size: cover;
   display: inline-block;
   position: fixed;
   top: 10px;
   right: 7px;
   z-index:4;
`

const MobileExtendSearchButton = styled.div `
    width: 32px;
  height: 32px;
  position: absolute;
  display: inline-block;
  z-index: 2;
  top: 10px;
  left: 10px;
`

const ExpandButton = styled(ExpandBtnImg)`
  height: 30px;  
  width: 30px;
  z-index: 10;
  display: inline-block;
  position: absolute;
  top: 5px;
  right: 10px;
  transition: transform 0.3s ease-out;
  transform: ${props => (props.active ? `rotate(270deg)` : `rotate(90deg)`)};
`

const HeaderMenu = styled(Link)`
  width: 100vw;
  height: 45px;
  display: block;
  font-size: x-large;
  color: ${props => (props.active ? `#748cc5` : `#6897ff`)};
`

const MobileMenuSlider = styled.div`
  transform:  ${props =>!props.active ?
  (contains(props.path, ['/blog','/post','/category'])?`translateY(-45px)`:
    (props.path.includes('/aboutme')?`translateY(-90px)`:
      (props.path.includes('/sandbox')?`translateY(-135px)`:
          `translateY(0px)`
      ))):`translateY(0px)`};
  transition: transform 0.2s ease-out;
`

const Separator = styled(MenuSeparator)`
  width: 100vw;
  display: block;
`

const bgstyle = {
  background: "#fefefe",
  transform: "rotate(180deg) translateY(100%)",
};

class HeaderElement extends React.Component {
  state = {
    isExpanded: false,
    isSearchMode: false
  };

  constructor(props) {
    super(props);
  }

  // function to handle the click
  expandClick = () => {
    this.setState(state => ({ isExpanded: !state.isExpanded }));
  };

  foldClick = () => {
    this.setState(state => ({ isExpanded: false }));
  };

  expandSearch = () =>{
    this.setState(state => ({ isSearchMode: !state.isSearchMode }));
  }

  render() {
    let data = this.props;
    return (
      <>
      <THeader>
        <MainLink>
          <Banner to="/" >
            <BrandIcon fluid={data.siteIcon.childImageSharp.fluid}   />
            <HeaderTitle>{data.siteTitle}</HeaderTitle>
          </Banner>
        </MainLink>
        <LinkRow active={this.state.isExpanded}>
          <Location>
            {({ location }) => {
              return(
                <>
                  <BrowserView>
                  <HeaderUl path={location.pathname} >
                    <HeaderLi>
                      <HeaderLink active={location.pathname==="/"?1:0}  to="/" >Home</HeaderLink>
                    </HeaderLi>
                    <HeaderLi>
                      <HeaderLink active={(contains(location.pathname, ['/blog','/post','/category']))?1:0 } to="/blog" >Blog</HeaderLink>
                    </HeaderLi>
                    <HeaderLi>
                      <HeaderLink active={location.pathname.includes("/aboutme")?1:0}  to="/aboutme" >About Me</HeaderLink>
                    </HeaderLi>
                    <HeaderLi>
                      <HeaderLink active={location.pathname.includes("/sandbox")?1:0}  to="/sandbox" >Sandbox</HeaderLink>
                    </HeaderLi>
                  </HeaderUl>
                    <LinkShadow path={location.pathname}><SubLinkShadow1/><SubLinkShadow2/></LinkShadow>
                  </BrowserView>
                  <MobileView>
                    <MobileMenuSlider active={this.state.isExpanded} path={location.pathname}>
                    <HeaderMenu active={location.pathname==="/"?1:0}  to="/" onClick={this.foldClick} >Home</HeaderMenu>
                    <HeaderMenu active={(contains(location.pathname, ['/blog','/post','/category']))?1:0 } to="/blog" onClick={this.foldClick} >Blog</HeaderMenu>
                    <HeaderMenu active={location.pathname==="/aboutme"?1:0}  to="/aboutme" onClick={this.foldClick} >About Me</HeaderMenu>
                    <HeaderMenu active={location.pathname==="/sandbox"?1:0}  to="/sandbox" onClick={this.foldClick} >Sandbox</HeaderMenu>
                    </MobileMenuSlider>
                    <ExpandButton id="ExpandSelector" onClick={this.expandClick}  active={this.state.isExpanded?1:0} />
                  </MobileView>
                </>
              )
            }}
          </Location>
        </LinkRow>
        <MobileView style={bgstyle}>
          <Separator/>
        </MobileView>
        <BrowserView>
          <Location>
            {({ location }) => {
              return (
                <>
                  {(contains(location.pathname, ['/blog','/post','/category'])) && (
                    <SearchBar>
                      <Search collapse indices={searchIndices} />
                    </SearchBar>
                  )}
                </>
              )
            }}
          </Location>
        </BrowserView>
      </THeader>
        <MobileView>
          <Location>
            {({ location }) => {
              return (
                <>
                  {(contains(location.pathname, ['/blog','/post','/category'])) && (
                    <>
                      <MobileSearchBar  active={this.state.isSearchMode}   >
                        <MobileExtendSearchButton onClick={this.expandSearch} />
                          <Search collapse indices={searchIndices} />
                      </MobileSearchBar>
                      <MobileSearchBarCap />
                    </>
                  )}
                </>
              )
            }}
          </Location>
        </MobileView>
      </>
    )
  }
}

function contains(target, pattern){
  let value = 0;
  pattern.forEach(function(word){
    value = value + target.includes(word);
  });
  return (value === 1)
}


export default HeaderElement
