import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql, Link } from "gatsby"
import Header from "../components/header"
import "./layout.css"
import styled from "styled-components"
import Footer from "../components/footer"
import posed, { PoseGroup } from "react-pose"

import CategoryList from "../components/category_list"
import {StyledButton} from "../components/style"

const timeout = 350
const delay = 350



const Main = styled.div`
  text-align: center;
  margin: 50px auto;
  @media only screen and (max-width: 768px) {
    margin-top: 100px;
  }
`

const Wrapper = styled.div`
  display: inline-block;
`

const MainBody = styled.div`
padding: 0;
  @media only screen and (max-width: 768px) {
    overflow-x: hidden;
  }
`
const PagingFooter = styled.div`
  width: 100%;
  height: 44px;
  z-index: 5;
  bottom: 0;
  left: 0;
  position: fixed;
  text-align: center;
  background-color: #f1f1f1;
  border-top: 1px solid #bebebe;
  ul {
  padding: 0;
  }
`

const Paging = styled.ul`
  margin: 0 auto;
  li {
    display: inline-block;
    padding: 5px 10px;
  }
`
const FooterLink = styled(Link)`
  display: inline-block;
  vertical-align: middle;
  color: #6897ff;
  border: 1px solid #6897ff;
  width: 30px;
  height: 30px;
  -webkit-box-shadow: 1px 2px 2px 0 rgba(0,0,0,0.26);
  -moz-box-shadow: 1px 2px 2px 0 rgba(0,0,0,0.26);
  box-shadow: 1px 2px 2px 0 rgba(0,0,0,0.26);
   transition: transform 0.2s ease-out;
  
  &:hover{
    transform: translateY( -3px) scale(1.03);
    transition: transform 0.3s ease-out;
    color: #df4d55;
    border: 1px solid #df4d55;
  }
  
`
//  color: #DD8825;
const ActiveLink = styled(FooterLink)`
  font-weight: bold;
  font-size: large;
  border: 1px solid #748cc5;
  width: 30px;
  height: 30px;
  color: #748cc5;
`
//  color: #e4e4e4;
const NonActiveLink= styled(FooterLink)`

`

const RoutesContainer = posed(Wrapper)({
  enter: {
     delay: delay,
     opacity: 1,
     delayChildren: timeout,
     transition: { ease: 'easeOut', duration: timeout}
  },
  exit: {
     opacity: 0,
     delayChildren: delay+timeout,
     transition: { ease: 'easeOut', duration: timeout }
  },
})

const Content = styled.div`
  display: inline-block;
  width: 1000px;
  @media only screen and (min-width: 1400px) {
    width: 1280px;
  }
  
  @media only screen and (max-width: 768px) {
    width: 100%;
  }
`


const Layout = ({ children, location, pageContext }) => (
  <StaticQuery
    query={graphql`query SiteTitleQuery {
        site {
          siteMetadata {
            title
          }
        }
        titleIcon: file(relativePath: { eq: "siteicon.png" }) {
          childImageSharp {
            fluid(maxWidth: 30) {
              ...GatsbyImageSharpFluid
            }
          }
        }
     
      }`}
    render={data => (
      <MainBody>
        <Header siteTitle={data.site.siteMetadata.title} siteIcon = {data.titleIcon} />

        <Main>
            <Content>
              {(pageContext.layout === "blog") && (
                <CategoryList currentPage={pageContext}/>
              )}
              <PoseGroup>
                <RoutesContainer key={location.pathname}>{children}</RoutesContainer>
              </PoseGroup>
            </Content>
        </Main>

        {pageContext.layout !== "blog" && (
          <Footer siteOwner={"Hourai Etealune"} />
        )}

        {pageContext.layout === "blog" && (
          <PagingFooter>
            <Paging>
              {pageContext.currentPage!==1 && (
                <li key={'prevpage'}>
                  <NonActiveLink to={pageContext.currentPage - 1 === 1 ? "/"+pageContext.nPath+"/" : "/"+pageContext.nPath+"/" + (pageContext.currentPage - 1).toString()} rel="prev">
                    &lt;
                  </NonActiveLink>
                </li>
              )}
              {Array.from({ length: pageContext.numPages }, (_, i) => (
                <li key={'page'+i}>
                  { pageContext.currentPage===i+1 && (
                    <ActiveLink to={`/${pageContext.nPath}/${i === 0 ? "" : i + 1}`}>
                      {i + 1}
                    </ActiveLink>
                  )}
                  { pageContext.currentPage!==i+1 && (
                    <NonActiveLink to={`/${pageContext.nPath}/${i === 0 ? "" : i + 1}`}>
                      {i + 1}
                    </NonActiveLink>
                  )}
                </li>
              ))}
              {pageContext.currentPage!==pageContext.numPages && (
                <li key={'nextpage'}>
                  <NonActiveLink to={"/"+pageContext.nPath+"/" + (pageContext.currentPage + 1).toString()} rel="next">
                    &gt;
                  </NonActiveLink>
                </li>
              )}
            </Paging>
          </PagingFooter>
        )}
      </MainBody>
    )}
  />
)
Layout.propTypes = {
    children: PropTypes.node.isRequired,
}
export default Layout