import styled, { keyframes } from "styled-components"
import { Link } from "gatsby"
import {
  BrowserView,
  MobileView,
} from "react-device-detect";

export const IBrowserView = styled(BrowserView)`
  display: inline-block;
`

export const IMobileView = styled(MobileView)`
  display: inline-block;
`

export const StyledButton = styled(Link)`
  background-color: #3c3c3c;
  width: 50px;
  height: 50px;
  margin-left: 20px;
  margin-bottom: 5px;
  color: #f1f1f1;
  cursor: pointer;
  background-position: center center;
  background-repeat: no-repeat;
  display: block;
    -webkit-box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  -moz-box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  box-shadow: 1px 4px 5px 0 rgba(0,0,0,0.26);
  border: 1px #999999 solid;
  
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    opacity: 0;
    -webkit-box-shadow: 2px 7px 10px rgba(0,0,0,.26);
    -moz-box-shadow: 2px 7px 10px rgba(0,0,0,.26);
    box-shadow: 2px 7px 10px rgba(0,0,0,.26);
    transition: opacity 0.2s ease-out;
  }

  &:hover{
    transform: translateY( -2px) scale(1.01);
    transition: transform 0.3s ease-out;
    &:after {
      opacity: 1;
      transition: opacity 0.2s ease-out;
    }
  }
`

export const LinkWithIcon = styled(Link)`
  text-decoration: none;
  color:  ${props => (props.active ? `#748cc5` : `#6897ff`)};
  font-weight: ${props => (props.active ? `bold` : `normal`)};;
  span {
    vertical-align: middle;
  }
  svg {
    margin-right: 3px;
    height: 17px;
    fill:  ${props => (props.active ? `#748cc5` : `#6897ff`)};
    vertical-align: middle;
  }

  div{
    display: inline-block;
    border-bottom: ${props => (props.active ? `1px solid #748cc5` : `0`)};
  }    

  &:hover {
    color: #df4d55;
   // border-bottom: 1px dotted #df4d55;
    svg {
      fill: #df4d55;
    }
    div {
      border-bottom: 1px dotted #df4d55;
    }
  }
`

export const SidebarLinkWithIcon = styled(Link)`
  text-decoration: none;
  color:  #7c7c7c;
  font-weight: ${props => (props.active ? `bold` : `normal`)};
  span {
    vertical-align: middle;
  }
  svg {
    margin-right: 5px;
    height: 20px;
    fill:  ${props => (props.active ? `#df4d55` : `#7c7c7c;`)};
    vertical-align: middle;
  }

  div{
    display: inline-block;
    height: 30px;
    border-bottom: ${props => (props.active ? `1px solid #748cc5` : `0`)};
  }    

  &:hover {
    svg {
      fill: #df4d55;
      padding-left: 5px;
    }
    div {
      &:hover {
      color:  #7c7c7c;
        border-right: 5px solid #748cc5;
        background-color: #eeeefe;
      }
    }
  }
`

