const opacity = keyframes`
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
`;

const CategoryPanel = styled.div`
  display: inline-block;
  position: sticky;
  top: 50px;
  left: 0;
  vertical-align: top;
  height: calc(100vh - 51px);
  border-right: 1px solid #bebebe;
   margin-bottom: -51px;
   @media only screen and (max-width: 768px) {
      position: fixed;
      left: -247px;
      top: 94px;
      transform: ${props => (props.active ? `translateX(100%)` : `translateX(0)`)};
      transition: transform 0.3s ease-out;
      z-index: 5;
      background-color: #fefefe;
      border-top: 1px solid #cecece;
    }
   
`

const CategoryWrapper = styled.div`
text-align: left;
  width: 244px;
  flex-wrap: wrap;
  display: inline-flex;
  position: sticky;
  top: 0;
  left: 0;
  vertical-align: top;  
  background-color: #fefefe;
  margin-top: 5px;
  margin-right: 2px;
  animation: ${opacity} 2s ease-out 0s forwards;
`

const SideBarContent = styled.div`
  padding: 5px 0;
  width: 244px;
  display: inline-block;
  ul {
    list-style: none;
    padding-left: 0;
    text-align: left;
    margin: 2px auto;
  }
`
const PostCount = styled.span`
  float: right;
  line-height: 28px
`

const Category = styled(SidebarLinkWithIcon)`

`

const CategoryLink = styled.div`
  width: 212px;
  height: 25px;
  text-decoration: none;
  padding-right: 5px;
`

const AllPostLink = styled.div`
  width: 237px;
  height: 25px;
  text-decoration: none;
  padding-right: 5px;
`

const CategoryName = styled.span`
  vertical-align: middle;
`

const PointerIcon = styled.img`
  height: 17px;
  vertical-align: middle;
  display: inline-block;
  margin-right: 5px;
`

const SideBarTitle = styled.div`
  display: inline-block;
  margin-top: 5px;
`

const SideBarTitleText = styled.span`
  display: block;
  letter-spacing: 2px;
  padding-left: 10px;
`

const SubCategory = styled.div`
  margin-left: 25px;
`

const PullTab = styled.div`
  width: 35px;
  height: 35px;
  background-color: #fefefe;
  position: fixed;
  left: 10px;
  top: 55px;
  z-index: 4;
  background-image: url(${burgerMenu});
  background-size: cover;
`

const SubCategoryElement = (exData) => {
  const {currentCategory, clickFunction} = exData
  return (
    <StaticQuery query={
      graphql`
        query {
          allMarkdownRemark(limit: 2000) {
            totalCount
            group(field: frontmatter___categories) {
              fieldValue
              totalCount
            }
          }
        }
      `}  render={data => (
        <>
          <CategoryWrapper>
            <SideBarContent>
              <SideBarTitleText>HOURAI'S BLOG</SideBarTitleText>
              <SubCategory>
                <ul>
                  <li>
                    <Category to={`/blog/`}  active={(currentCategory === undefined)?1:0} onClick={clickFunction} >
                      <CategoryLink>
                        <AllIcon/>
                        <CategoryName>All Posts</CategoryName>
                        <PostCount>{data.allMarkdownRemark.totalCount}</PostCount>
                      </CategoryLink>
                    </Category>
                  </li>
                </ul>
              </SubCategory>
              <SideBarTitle>
                <SideBarTitleText>CATEGORIES</SideBarTitleText>
              </SideBarTitle>
              <SubCategory>
                <ul>
                  {data.allMarkdownRemark.group.map(category => (
                    <li key={category.fieldValue}>
                      <Category onClick={clickFunction} to={`/category/${snakeCase(category.fieldValue)}/`}  active={(currentCategory === category.fieldValue)?1:0}>
                        <CategoryLink>
                          <TagIcon/><CategoryName>{category.fieldValue}</CategoryName><PostCount>{category.totalCount}</PostCount>
                        </CategoryLink>
                      </Category>
                    </li>
                  ))}
                </ul>
              </SubCategory>
            </SideBarContent>
          </CategoryWrapper>
        </>
    )}
    />)
}

class CategoryList extends React.Component  {
  // ... constructor and toggleMenu from above
  state = {
    isExpanded: false
  };

  constructor(props) {
    super(props);
  }

  // function to handle the click
  expandClick = () => {
    this.setState(state => ({ isExpanded: !state.isExpanded }));
  };

  foldClick = () => {
    this.setState(state => ({ isExpanded: false }));
  };

  render() {
    return (
      <>
      <CategoryPanel active={this.state.isExpanded?1:0} >
        <SubCategoryElement currentCategory={this.props.currentPage.category} clickFunction = {this.foldClick} />
      </CategoryPanel>
        <MobileView>
          <PullTab onClick={this.expandClick}  active={this.state.isExpanded?1:0} />
        </MobileView>
      </>
    )
  }
}

export default CategoryList
